@import "../../../style-conf/mixins.scss";
@import "../../../style-conf/variable.scss";
.call-to-action {
    height: 27px;
    width: fit-content;
    padding: 0 5px;
    background: $white;
    border-radius: 6px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: $black-light;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    margin: 0 15px;
    @include typography(18px, 400, 1);
    .text,
    .arrow-icon {
        position: relative;
        z-index: 1;
        transition: all ease 500ms;
    }
    .icon {
        width: 21px;
        height: 22px;
        background-color: rgba(255, 0, 0, 0.641);
        border-radius: 41px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-left: 10px;
        font-style: 12px;
    }
    &:before {
        content: "";
        position: absolute;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $black;
        z-index: 0;
        transform: translateX(-100%);
        transition: all 300ms cubic-bezier(0.55, 0.055, 0.675, 0.19);
    }
    &:hover {
        &:before {
            transform: translateX(0);
        }
        .arrow-icon,
        .icon {
            background-color: $white;
            color: black;
        }
        .text {
            color: white;
        }
        .icon {
            color: rgb(10, 10, 0);
            background-color: beige;
            z-index: 2;
        }
    }
}
