@import "../../style-conf/mixins.scss", "../../style-conf/variable.scss";

.contact-content-wrapper {
    display: flex;
    width: 100%;
    height: 40vh;
    margin-top: 25px;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    @include small-screen {
        margin-top: 75px;
    }
    @media (max-width: 1224px) {
        .contact-content-wrapper {
            padding: 20px;
            height: 50%;
        }
    }
}
