@import "../../../style-conf/mixins.scss";
@import "../../../style-conf/variable.scss";
.top-nav-bar {
    width: 85%;
    align-items: center;
    display: flex;
    justify-content: space-between;
    margin: 60px;
    .app-logo {
        width: 143px;
        img {
            width: 100%;
            display: block;
        }
    }
    .navigation {
        display: flex;
        align-items: center;
        .navigation-item {
            color: $white;
            @include typography(18px, 400, 1);
            margin-left: 20px;
            padding: 5px;
            box-shadow: inset 0 0 0 $white;
            transition: color 0.4s ease-in-out, box-shadow 0.4s ease-in-out;
            cursor: pointer;
            &:hover {
                box-shadow: inset 200px 0 0 0 $white;
                color: $black;
            }
        }
    }
}
