@import url("https://fonts.googleapis.com/css2?family=DM+Sans:opsz,wght@9..40,400;9..40,500;9..40,700&family=Montserrat:ital,wght@0,400;1,600&family=Poppins:ital,wght@0,400;1,600&family=Victor+Mono:ital,wght@0,100;1,600&display=swap");

@import "./style-conf/mixins.scss";
@import "./style-conf/variable.scss";

* {
    margin: 0;
    padding: 0;
    font-family: "DM Sans", sans-serif;
    box-sizing: border-box;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

p {
    @include typography(18px, 400, 24px);
}

h2 {
    @include typography(42px, 700, 1.2);
}

h3 {
    @include typography(26px, 500, 1.2);
}

h4 {
    @include typography(22px, 500, 1.2);
}
