@import "../../../style-conf/mixins.scss", "../../../style-conf/variable.scss";

.contact-info-box {
    width: 100%;

    background-color: $black-lighter;
    padding: 15px;
    border-radius: 6px;
    color: $gray;

    @include small-screen {
        padding: 30px;
        height: auto;
    }
    h4 {
        line-height: 24px;
        margin-bottom: 20px;
        @include small-screen {
            line-height: 34px;
        }
    }
    .contact-option {
        display: flex;
        align-items: center;
        margin-top: 15px;
        svg {
            color: $yellow;
            font-size: 16px;
        }
        .text {
            color: $white;
            margin-left: 15px;
        }
    }
}
