.intro-section {
    width: 100%;
    height: 100vh;
    overflow: hidden;
    background: linear-gradient(
        121.46deg,
        #8647d2 9%,
        #661cc8 20%,
        #2916d3 80%
    );
    position: relative;
    display: flex;
    justify-content: center;
    .vector-bg {
        width: 100%;
        height: 100vh;
        position: absolute;
        background-image: url("../../images//vector-background.png");
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
        z-index: 0;
        opacity: 0.4;
    }
    .cloud {
        position: absolute;
        bottom: 0;
        z-index: 2;
        left: 0;
        width: 100%;
    }
    .cloud-soft {
        position: absolute;
        bottom: 0;
        z-index: 1;
        left: 0;
        width: 100%;
        mix-blend-mode: soft-light;
    }
    .content {
        width: 100%;
        padding: 0 20px;
        position: relative;
        display: flex;
        flex-direction: column;
    }
}
